.form-container{
    text-align: center;
    width: 60% !important;
    margin-top: 100px;
    padding: 0px;
}
.table-container{
    text-align: center;
    width: 60% !important;
    padding: 0px;
    max-height: 70%;
    overflow: auto;
  }
.employee-table{
    background: #fff;
  }

.title{
    color:#fff;
    float: left;
    font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-size: 22px;
    font-weight: 600;
    margin-top: 5px;
  }
.add-btn{
    float: right;
    margin-bottom: 10px;
  }
.view-btn{
    float: right;
    padding-top: 2px !important;
    padding-bottom: 2px !important;
    margin-right: 10px;
  }
.table-header{
    background: -webkit-linear-gradient(to top,rgb(235, 78, 6),rgb(240, 130, 67));
    background: linear-gradient(to top,rgb(235, 78, 6),rgb(240, 130, 67));
    color: #fff;
}

@media screen and (max-width: 550px) {
  .form-container{
    text-align: center;
    width: 90% !important;
    margin-top: 100px;
    padding: 0px;
}
.table-container{
  text-align: center;
  width: 90% !important;
  padding: 0px;
  overflow: auto;
}
}